.pagination {
    display: flex;
    list-style: none;
    outline: none;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    padding: 8px;
    .active{
        background-color: #615dfa;
        color: #ffffff;
    }
    li {
        margin: 2px ;
        background-color: #ffffff;
        font-size: 20px;
        font-weight: 600;
        color: #9e9faf;
        border-radius: 5px;
span{
    background-color: #ffffff;
    font-size: 20px;
    font-weight: 600;
    color: #9e9faf;
    padding: 10px 17px;
    border-radius: 5px;
};
    }
}
.pagination > .active > a{
    background-color: #615dfa;
    color: #ffffff;
}
.pagination > li > a{
    border: 1px solid var(--primery) ;
    padding: 5px 10px;
    outline: none;
    cursor: pointer;
}
.pagination > .active > a, .pagination > .active > span, .pagination > .active > a:hover, .pagination > .active > span:hover, .pagination > .active > a:focus, .pagination > .active > span:focus{
    background-color: var(--primery) ;
    border-color: var(--primery);
    outline: none ;
}
.pagination > li > a, .pagination > li > span{
    background-color: #ffffff;
    font-size: 20px;
    font-weight: 600;
    color: #9e9faf;
    padding: 10px 17px;
    border-radius: 5px;
}
.pagination > li:first-child > a, .pagination > li:first-child > span, .pagination > li:last-child > a, .pagination > li:last-child > span{
    border-radius: unset
}
.wrapper{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: inherit;
    .container{
        display: flex;
        flex-wrap: wrap;
        justify-content: space-evenly;
        align-items: center;
    }
}